.panel {
    height: 100vh;
    width: 320px;

    .tab {
        margin: 10px;
        padding: 10px;
        border-radius: 5px;
        background: white;
        box-shadow: 0 7px 14px rgba(0, 0, 0, 0.15);

        p {
            font-weight: 700;
            margin: 0;
            padding: 5px;
        }
    }

    .checker,
    .radio,
    .input {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        padding: 2px 10px;
        margin: 0;

        span {
            font-size: 14px;
            user-select: none;
            flex-grow: 2;
        }

        input {
            size: 16px;
            margin: 0;
            flex-grow: 0;
        }
    }

    .input input {
        width: 87%;
    }

    .disabled {
        pointer-events: none;
        opacity: 0.4;
    }

    .error {
        outline: 2px solid red;
    }
}
